import { Helmet } from 'react-helmet-async';
import { Link as RouterLink } from 'react-router-dom';
import { Grid, Container, Typography, Button, Stack, Tooltip } from '@mui/material';
import AddIcon from '@mui/icons-material/Add';

import PageTitleWrapper from 'src/components/PageTitleWrapper';
import Footer from 'src/components/Footer';
import CustomerTable from './CustomerTable';
import Content from './Content';

import {
  useGetInventoriesFromUserQuery
} from 'src/services/baseApi';

function CustomerList() {

  const { data = [], isLoading } = useGetInventoriesFromUserQuery(
    {
      userId: 1, // MOCK
    },
    { refetchOnMountOrArgChange :true, refetchOnFocus: true }
  );

  if (isLoading) return (<></>);

  return (
    <>
      <Helmet>
        <title>Clientes</title>
      </Helmet>
      <PageTitleWrapper>
        <Grid container justifyContent="space-between" alignItems="center">
          <Grid item xs={6} sm={6} md={6}>
            <Typography variant="h3" component="h3" gutterBottom>
              Lista de Inventários
            </Typography>
            <Typography variant="subtitle2">
              Lista de clientes e inventários cadastrados
            </Typography>
          </Grid>
          <Grid item>
            <Stack spacing={1} direction="row">
              <Tooltip arrow title="Cadastrar novo inventário">
                <Button
                  sx={{ mt: { xs: 2, md: 0 } }}
                  variant="contained"
                  startIcon={<AddIcon fontSize="small" />}
                  component={RouterLink}
                  to="/inventory/datacustomer"
                >
                Criar Inventário
                </Button>
              </Tooltip>
            </Stack>
          </Grid>
        </Grid>
      </PageTitleWrapper>
      <Container maxWidth="lg">
        <Grid
          container
          direction="row"
          justifyContent="center"
          alignItems="stretch"
          spacing={3}
        >
          <Grid item xs={12}>
            {/* <CustomerTable /> */}
            <Content
              data={data}
            />
          </Grid>
        </Grid>
      </Container>
      <Footer />
    </>
  );
}

export default CustomerList;
