import {
    Button,
    Card,
    CardContent,
    Divider,
    Grid,
    Typography,
    Avatar,
    Tooltip,
    IconButton,
    lighten,
    styled
} from '@mui/material';
import { Inventory } from 'src/types';
import Box from '@mui/material/Box';

import EditTwoToneIcon from '@mui/icons-material/EditTwoTone';

import PersonIcon from '@mui/icons-material/Person';
import AccountBoxIcon from '@mui/icons-material/AccountBox';
import FamilyRestroomIcon from '@mui/icons-material/FamilyRestroom';
import HomeWorkIcon from '@mui/icons-material/HomeWork';
import DirectionsCarIcon from '@mui/icons-material/DirectionsCar';
import SavingsIcon from '@mui/icons-material/Savings';
import GroupsIcon from '@mui/icons-material/Groups';
import AttachMoneyIcon from '@mui/icons-material/AttachMoney';
import AutoAwesomeMotionIcon from '@mui/icons-material/AutoAwesomeMotion';
import SecurityIcon from '@mui/icons-material/Security';
import PublicIcon from '@mui/icons-material/Public';
import AssessmentIcon from '@mui/icons-material/Assessment';
import PictureAsPdfIcon from '@mui/icons-material/PictureAsPdf';

import Text from 'src/components/Text';
import { formatDate } from 'src/services/utils';

import CardItem from './CardItem';
import CardItemAdd from './CardItemAdd';

const AvatarPrimary = styled(Avatar)(
  ({ theme }) => `
      background: ${theme.colors.primary.lighter};
      color: ${theme.colors.primary.main};
      width: ${theme.spacing(6)};
      height: ${theme.spacing(6)};
`
);

const IconButtonEdit = styled(IconButton)(
  ({ theme }) => `
      background: ${theme.colors.secondary.lighter};
      color: ${theme.colors.secondary.main};
      padding: ${theme.spacing(0.5)};

      &:hover {
      background: ${lighten(theme.colors.secondary.lighter, 0.4)};
      }
  `
);

export default function Content(props) {

  let inventories: Inventory[] = JSON.parse(JSON.stringify(props.data))
  if (inventories == null) {
    return (
      <>
        <h4>Carregando...</h4>
      </>
    )
  }

  return (
    <Grid container spacing={3}>
      <Grid item xs={12} sm={12} md={12}>
        <Card>
          {/* <Box
            p={3}
            display="flex"
            alignItems="flex-start"
          >
            <AvatarPrimary>
              <PublicIcon />
            </AvatarPrimary>
            <Box pl={2}>
              <Typography variant="h4" gutterBottom>
                Inventários
              </Typography>
              <Typography variant="subtitle2">
                Gerencie bens no exterior
              </Typography>
            </Box>
          </Box> */}

          {/* <Divider /> */}

          <CardContent sx={{ p: 3 }}>
            <Typography variant="subtitle2">
            <Grid container spacing={3}>
              {inventories.map(function (inventory, idx) {
                  return (
                    <CardItem
                      data={{
                        title: "Inventariado: " + inventory.inventoried.name,
                        subtitle: "Solicitante: " + inventory.customer.name,
                        id: inventory.id,
                        labels:[
                          "Cadastro: " + inventory.id
                        ]
                      }}
                    />
                  )
                })}
                {/* <CardItemAdd/> */}
            </Grid>
            </Typography>
          </CardContent>

        </Card>
      </Grid>
    </Grid>
  );
}